body {
  margin: 0;
  text-align: center;
  font-family: Helvetica;
  background-color: #60b28e;
}

/* header area */
.banner {
  text-align: center;
  width: 600px;
  height: 100px;
  margin: auto;
  background-color: #264F3D;
  border-radius: 5px;
  -webkit-box-shadow: 3px 3px 2px black;
          box-shadow: 3px 3px 2px black;
}
.title {
  line-height: 100px;
  color: white;
  font-weight: bolder;
  font-size: 2.5em;
}

/* app area */
.app {
  height: auto;
  margin: auto;
  margin-top: 40px;
  margin-bottom: 40px;
}

/* Board */
button {
  padding: 10px;
  margin: 20px;
  width: 100px;
  border-radius: 0;
}
.board {
  margin: 0;
}
.dead {
  fill: #336951;
  border: 0;
  -webkit-transition: fill 0.2s;
  -o-transition: fill 0.2s;
  transition: fill 0.2s;
}
.dead:hover {
  cursor: crosshair;
}
.live {
  fill: red;
  border: 0;
  -webkit-transition: fill 0.2s;
  -o-transition: fill 0.2s;
  transition: fill 0.2s;
}
.live:hover {
  cursor: crosshair;
}



